'use client';

import { useState } from 'react';
import Link from 'next/link';

interface Traveler {
    id: string;
    name: string;
    rating: number;
    trustLevel: string;
    price: number;
    date: string;
    avatar: string;
    route: string;
    isBestMatch?: boolean;
}

export default function SelectTravelerPage() {
    const [selectedTraveler, setSelectedTraveler] = useState<string | null>(null);

    const travelers: Traveler[] = [
        {
            id: '1',
            name: 'Anna K.',
            rating: 4.8,
            trustLevel: '4.8/5.0 Trust Level',
            price: 65,
            date: 'May 20',
            avatar: '/api/placeholder/40/40',
            route: 'Warsaw → Gdańsk',
            isBestMatch: true
        },
        {
            id: '2',
            name: 'Maciej S.',
            rating: 4.6,
            trustLevel: '4.6/5.0 Trust Level',
            price: 55,
            date: 'May 19',
            avatar: '/api/placeholder/40/40',
            route: 'Warsaw → Gdańsk'
        },
        {
            id: '3',
            name: 'Piotr M.',
            rating: 4.5,
            trustLevel: '4.5/5.0 Trust Level',
            price: 50,
            date: 'May 18',
            avatar: '/api/placeholder/40/40',
            route: 'Warsaw → Gdańsk'
        }
    ];

    return (
        <div className="min-h-screen bg-black text-white flex flex-col">
            {/* Status Bar */}
            <div className="flex justify-between items-center p-4 pt-12 text-sm">
                <span>9:41</span>
                <div className="flex items-center gap-1">
                    <div className="flex gap-1">
                        <div className="w-1 h-1 bg-white rounded-full"></div>
                        <div className="w-1 h-1 bg-white rounded-full"></div>
                        <div className="w-1 h-1 bg-white rounded-full"></div>
                        <div className="w-1 h-1 bg-white rounded-full"></div>
                    </div>
                    <svg className="w-4 h-4" viewBox="0 0 24 24" fill="white">
                        <path d="M2 17h20v2H2v-2zm1.15-6.05L4 9.47l.85 1.48H5c.55 0 1 .45 1 1s-.45 1-1 1-.45-.45-.45-1zm5.5 2.5H9c.55 0 1-.45 1-1s-.45-1-1-1-.45.45-.45 1z" />
                    </svg>
                    <div className="w-6 h-3 border border-white rounded-sm">
                        <div className="w-4 h-1.5 bg-white rounded-sm m-0.5"></div>
                    </div>
                </div>
            </div>

            {/* Header */}
            <div className="text-center px-8 mb-8">
                <h1 className="text-4xl font-bold mb-2">Pickom</h1>
                <p className="text-gray-400 text-lg">People-Powered Delivery</p>

                <h2 className="text-2xl font-bold mt-8 mb-2">Warsaw → Gdańsk</h2>
            </div>

            {/* Best Match Badge */}
            <div className="px-8 mb-4">
                <div className="flex items-center gap-2">
                    <span className="text-orange-500 font-medium">Best Match</span>
                    <div className="w-6 h-6 bg-orange-500 rounded-full flex items-center justify-center">
                        <svg className="w-4 h-4 text-white" viewBox="0 0 24 24" fill="currentColor">
                            <path d="M12 2L15.09 8.26L22 9L17 14L18.18 21L12 17.77L5.82 21L7 14L2 9L8.91 8.26L12 2Z" />
                        </svg>
                    </div>
                </div>
            </div>

            {/* Travelers List */}
            <div className="flex-1 px-8 space-y-4">
                {travelers.map((traveler, index) => (
                    <div
                        key={traveler.id}
                        className={`bg-gray-900 rounded-lg p-4 transition-all ${selectedTraveler === traveler.id ? 'ring-2 ring-orange-500' : ''
                            } ${index === 0 ? 'ring-2 ring-orange-500 ring-opacity-50' : ''}`}
                        onClick={() => setSelectedTraveler(traveler.id)}
                    >
                        <div className="flex items-center justify-between">
                            <div className="flex items-center space-x-3">
                                {/* Avatar */}
                                <div className="w-12 h-12 bg-gray-600 rounded-full flex items-center justify-center">
                                    <svg className="w-8 h-8 text-gray-300" viewBox="0 0 24 24" fill="currentColor">
                                        <path d="M12 2C13.1 2 14 2.9 14 4C14 5.1 13.1 6 12 6C10.9 6 10 5.1 10 4C10 2.9 10.9 2 12 2ZM21 9V7L15 1H5C3.9 1 3 1.9 3 3V21C3 22.1 3.9 23 5 23H19C20.1 23 21 22.1 21 21V9M19 21H5V3H13V9H19V21Z" />
                                    </svg>
                                </div>

                                <div>
                                    <h3 className="text-lg font-semibold text-white">{traveler.name}</h3>
                                    <div className="flex items-center space-x-1">
                                        <div className="w-2 h-2 bg-green-500 rounded-full"></div>
                                        <span className="text-gray-400 text-sm">{traveler.trustLevel}</span>
                                    </div>
                                </div>
                            </div>

                            <div className="text-right">
                                <div className="text-2xl font-bold text-white">€{traveler.price}</div>
                            </div>
                        </div>

                        <div className="mt-3 flex items-center justify-between">
                            <span className="text-gray-400">{traveler.date}</span>
                            <div className="flex items-center space-x-1">
                                <svg className="w-4 h-4 text-gray-400" viewBox="0 0 24 24" fill="currentColor">
                                    <path d="M3 13h8V3H3v10zm0 8h8v-6H3v6zm10 0h8V11h-8v10zm0-18v6h8V3h-8z" />
                                </svg>
                                <span className="text-gray-400 text-sm">{traveler.price}</span>
                            </div>
                        </div>
                    </div>
                ))}
            </div>

            {/* Continue Button */}
            <div className="p-8">
                <Link
                    href="/confirm-payment"
                    className="w-full bg-orange-500 text-white py-4 rounded-full text-lg font-medium text-center block hover:bg-orange-600 transition-colors disabled:opacity-50"
                >
                    Select Traveler
                </Link>
            </div>

            {/* Bottom Indicator */}
            <div className="flex justify-center pb-6">
                <div className="w-32 h-1 bg-white rounded-full"></div>
            </div>
        </div>
    );
} 